
ul.plain {
    list-style-type: none;
    margin: 0;
    padding: 0;

    li {
        margin-top: 1em;
    }
}

.post-link {
    font-weight: bold;
    font-style: italic;
    font-size: 1.2em;
    color: $text-color;
}

.post-link:hover {
    text-decoration: underline;
}

.post-link::before {
    content: '#';
    color: $light-text;
}

.intro {
    font-size: medium;
}

.tag-preview {
    font-style: italic;
    color: $light-text;
    text-decoration: none;

    a {
        font-style: italic;
        color: $light-text;
        text-decoration: none;
      }
    
      a:hover {
        text-decoration: underline;
      }
}

h3.with-line {
    display: flex;
    width: 100%;
    align-items: center;
  }
  
  h3.with-line:after {
    content: '';
    border-top: 1px solid lightgray;
    margin: 0 0 0 20px;
    flex: 1 0 20px;
  }
  
  