.tag-block {
  font-style: italic;
  color: $light-text;

  a {
    font-style: italic;
    color: $light-text;
    text-decoration: none;
  }

  a:hover {
    text-decoration: underline;
  }

  .previous, .next {
    text-align: center;
    margin: 1em;
  }
  
  .tags {
    text-align: center;
    margin: 1em;
  }

  @media screen and (min-width: $mobile-px) {
    display: flex;

    .previous, .next {
      flex-basis: 20%;
    }

    .tags {
      flex-basis: 60%;
    }
  }
}

.author {
  color: $light-text;
  font-size: 1em;
  .author-name{
    color: $text-color;
    font-size: 1.5em;
  }
}

.author::before {
  content: '@';
  font-size: 1.5em;
  color: $light-text;
}

.post-id::before {
  content: '#';
  color: $light-text;
}

.card {
  margin-top: 2em;
  margin-bottom: 2em;
  border-style: solid;
  padding: 1em;

  .card-title {
    font-weight: bold;
    border-width: thin;
  }
}