@font-face {
  font-family: 'Gentium Plus';
  src: url('fonts/GentiumPlus-R.ttf');
}
@font-face {
  font-family: 'Gentium Plus';
  src: url('fonts/GentiumPlus-I.ttf');
  font-style: italic;
}

html {
  background: $background-color;
}

body {
  margin: 0;
  padding: 0;
  color: $text-color;
  font-family: $font-family-main;
  font-size: $font-size;
  word-wrap: break-word;
}

h1,
h2,
h3,
h4,
h5,
h6,
.title {
  font-family: $font-family-headings;
  line-height: 1.3;
  margin: 0.6em 0 0 0;
  font-size: 1.3em;

  font-weight: bold;
  font-style: italic;

  a {
    color: $text-color;
  }

  a:hover {
    text-decoration: underline;
  }
}

@media screen and (min-width: $mobile-px) {
  .title {
    font-size: 2em;
  }

  h3 {
    font-size: 1.5em;
  }
}

.content-all {
  padding: 1em;
  max-width: 65ex;
  margin: auto;
}

.title-block {
  padding: 1em;
  max-width: 100ex;
  margin: auto;

  .title {
    text-align: center;
  }
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

.ext-link, .card-title {
  color: red;

  &:visited {
    color: darkred;
  }
}

