.site-title {
  font-weight: bold;
}

.big-nav, .small-nav {
  float: right;
}

.site-header {
  margin: 0.5em 1em 0.5em 1em;
}

.big-nav {
  display: none;
}

@media screen and (min-width: $mobile-px) {
  .big-nav {
    display: inline;
  }
  .small-nav {
    display: none;
  }
}

.full-page-nav {
  margin: auto;
  text-align: center;
}